import React from 'react';
import { NoticeBar, Grid, Badge, Toast } from 'antd-mobile';
import {withRouter} from 'react-router';

class Index extends React.Component {
  constructor(props){
    super(props);
    const selectedTab = props.selectedTab;

    this.state = {
      title: '拇指互刷',
      open: false,
      selectedTab
    };
  }
  render() {
    const styles = {
      subTitle: {
        color: '#888',
        fontSize: '14px',
        padding: '15px 0 9px 15px'
      }
    };
    const data = [
      {
        id: 'qq_zan',
        path: '/qq_zan',
        icon: 'data:image/png;base64,',
        text: `名片互赞`
      },
      {
        id: 'ss_zan',
        path: '/ss_zan',
        icon: 'data:image/png;base64,',
        text: '说说互赞'
      },
      {
        id: 'ss_comment',
        path: '/ss_comment',
        icon: 'data:image/png;base64,',
        text: '说说互评'
      },
      {
        id: 'qzone_zan',
        path: '/qzone_zan',
        icon: 'data:image/png;base64,',
        text: '空间主页赞'
      },
      {
        id: 'qzone_liulan',
        path: '/qzone_liulan',
        icon: 'data:image/png;base64,',
        text: '空间互访'
      },
      {
        id: 'qzone_comment',
        path: '/qzone_comment',
        icon: 'data:image/png;base64,',
        text: '空间留言'
      }
    ];
    const data2 = [
      {
        icon: 'data:image/jpeg;base64,',
        text: '官方Q群',
        type: 'qqgroup',
        fn() {
          window.Zan.addQQGroup('863391242');
        }
      },
      {
        icon: 'data:image/jpeg;base64,',
        text: '加群领赞',
        type: 'qqgroup',
        fn() {
          window.Zan.addQQGroup('863391242');
        }
      },
      {
        icon: 'data:image/png;base64,',
        text: '支付宝红包',
        type: 'url',
        fn() {
          window.Zan.openZfbHb('https://qr.alipay.com/c1x05442h0tzzssv8pfzbbe');
        }
      },
      {
        icon: 'http://ugc.qpic.cn/gbar_pic/2wF3sr2LiaVvBkYLQacXpHDdNu7icXncRAUJEsDqtJoS83mn8dhBXxkA/0',
        text: '拇指赞APP',
        type: 'url',
        fn() {
          window.Zan.openUrl('http://attach.zhiyoo.com/forum/201808/31/155246866dghgdkrlbt6lv.attach?f=%E5%8D%83%E5%AF%BB%E6%8B%87%E6%8C%87%E8%B5%9Ev1.1.apk');
        }
      },
      {
        icon: 'http://ugc.qpic.cn/gbar_pic/2wF3sr2LiaVvBkYLQacXpHDdNu7icXncRAUJEsDqtJoS83mn8dhBXxkA/0',
        text: '拇指赞代刷',
        type: 'url',
        fn() {
          window.Zan.openUrl('https://520cy.cn');
        }
      }
    ];
    return (
      <div>
        <NoticeBar marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
          通知: 加入QQ群8234343349免费领取Q赞！
        </NoticeBar>
        
        <div style={styles.subTitle}>常用工具</div>
        <Grid data={data} activeStyle={false} onClick={
          (el, idx) => {
            if (el.path) {
              if (el.path !== '/qq_zan' && localStorage['isLogin'] == 0) {
                Toast.info('启用该功能需要先登录QQ空间~');
                setTimeout(() => {
                  window.Zan.login("1");
                }, 1500);
                return false;
              }
              if (el.path == '/ss_zan' || el.path == '/ss_comment') {
                Toast.info('功能内测中，加入Q群提前体验~');
                return false;
              }
              this.props.router.push(el.path);
            } else {
              Toast.info('暂未开放，敬请期待~', 1.5);
              console.log('暂未实现!');
            }
          }
        }/>

        <div style={styles.subTitle}>
          推荐 <Badge text="hot" hot />
        </div>
        <Grid data={data2} activeStyle={false} onClick={
          (el, idx) => {
            if (typeof el.fn === 'function') {
              el.fn();
            }
          }
        }/>
      </div>
    );
  }
}

export default withRouter(Index);


